.footer {
	margin-top: auto;
	background: black;
	opacity: 0.95;
	color: white;
	display: flex;
	align-items: center;
	justify-content: center;
	white-space: nowrap;
	overflow: hidden;
	padding-bottom: 0.5rem;
}

.footer-row-wrapper {
	display: flex;
}

.footer-row {
	display: flex;
	align-items: center;
	justify-content: center;
}

.footer-content {
	position: relative;
	overflow: hidden;
	text-decoration: none;
	color: #fff;
	margin: 1rem;
	font-size: 1.2rem;
}
  
.footer-content::before {
	content: '';
	position: absolute;
	left: 10%;
	top: 45%;
	height: 55%;
	width: 90%;
	background: rgba(0, 110, 165, 0.9);
	border-radius: 5px;
	z-index: -1;
	transform-origin: 100% 50%;
	transform: scaleX(0);  
	transition: transform 0.2s ease-in-out;
}

.footer-content:hover::before {
	transform: scaleX(1);
	transform-origin: 0 50%;
}

.footer-content > h1 {
	font-size: 2rem;
	margin-bottom: 0.5rem;
}

.footer-row.brands {
	font-size: 2.1rem;
	align-items: center;
	padding-left: 3rem;
	align-self: center;
}

.brand-container {
	margin: 0 1rem;
}

.fa-brands {
	color: white;
	transition: all 0.2s ease-out;
}

.fa-brands:hover {
	opacity: 0.5;
}

@media screen and (max-width: 960px) {
	.footer {
		flex-direction: column;
	}

	.footer-row.brands {
		padding: 0.5rem 0 1rem 0;
	}
}

@media screen and (max-width: 800px) {
	.footer-row-wrapper {
		flex-direction: column;
	}
}

@media screen and (max-width: 450px) {
	.footer-content {
		font-size: 1rem;
		margin: 0.5rem;
	}
	.fa-brands {
		font-size: 1.7rem;
	}
}