.cards {
	padding: 4rem 4rem 3rem 4rem;
	background: rgba(0, 0, 0, 0.4);
  }
  
 .cards > h1 {
	text-align: center;
	color: rgba(255, 255, 255, 0.9);
  }
  
  .cards__container {
	display: flex;
	flex-flow: column;
	align-items: center;
	/* max-width: 1120px; */
	width: 90%;
	margin: 0 auto;
  }
  
  .cards__wrapper {
	position: relative;
	margin-top: 3rem;
	/* Patch for SimpleCardItem that's not bad */
	min-width: 75vw;
  }
  
  .cards__items {
	display: flex;
	flex-direction: row;
	margin-bottom: 24px;
  }
  
  .cards__item {
	display: flex;
	flex: 1;
	margin: 0 1rem;
	border-radius: 10px;
	/* background: rgba(0, 0, 0, 0.55); */
}
  
  .cards__item__link {
	display: flex;
	flex-flow: column;
	width: 100%;
	border-bottom: 1px solid rgb(200, 200, 200);
	/* box-shadow: 0 6px 20px rgba(150, 150, 150, 0.6); */
	border-radius: 10px 10px 4px 4px;
	overflow: hidden;
	text-decoration: none;
  }
  
  .cards__item__pic-wrap {
	position: relative;
	width: 100%;
	padding-top: 67%;
	overflow: hidden;
  }
  
  .cards__item__pic-wrap::after {
	content: attr(data-category);
	position: absolute;
	bottom: 0;
	margin-left: 10px;
	padding: 3px 8px 6px 8px;
	max-width: calc((100%) - 50px);
	font-size: 16px;
	font-weight: 600;
	color: rgba(255, 255, 255, 0.85);
	background-color: rgba(50, 50, 50, 0.9);
	box-sizing: border-box;
	border-radius: 8px 8px 0 0;
	font-family: 'Roboto Slab', 'Nunito', sans-serif;
  }
  
  .cards__item__img {
	position: absolute;
	top: 0;
	height: 100%;
	/* width: 100%;
	max-width: 100%;
	max-height: 100%; */
	object-fit: cover;
	transition: all 0.2s ease;
	border-radius: 10px;
	z-index: 0;
  }

  .cards__item__link:hover .cards__item__img {
	transform: scale(1.1);
  }

  .cards__item__info {
	padding: 20px;
  }
  
  .cards__item__text {
	text-align: center;
	font-size: 18px;
	line-height: 24px;
	color: rgba(255, 255, 255, 0.85);
  }
  
  @media only screen and (max-width: 960px) {
	.cards__items {
		flex-direction: column;
	}
	.cards__item {
		margin-bottom: 2rem;
	}
  }

  .cards__item__pic-wrap__simple {
	position: relative;
	padding-top: 67%;
	overflow: hidden;
  }

  .cards__item__pic-wrap__simple::after {
	content: attr(data-category);
	position: absolute;
	/* bottom: calc(48% - 0.5em);
	left: 10%; */
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	max-width: 100%;
	font-size: 375%;
	font-weight: 700;
	color: #fff;
	background-color: rgba(103, 81, 47, 0.25);
	border-radius: 10px;
	box-sizing: border-box;
	font-family: 'Roboto Slab', sans-serif;
}

.cards__item__pic-wrap__empty {
	position: relative;
	padding-top: 67%;
	overflow: hidden;
}

/* Used for Cmd-Fs */
.hidden {
	opacity: 0;
}

@media only screen and (max-width: 1200px) {
	.cards__item__pic-wrap__simple::after {
		font-size: 200%;
	}
}

.timeline__wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	scroll-snap-align: start;
	height: 100%;
}

.timeline__date {
	font-size: 1.3rem;
	padding-bottom: 1rem;
}

.cards__item.timeline {
	width: 97%;
}