img {
	object-fit: cover;
	top: -65px;
	width: 100%;
	height: 120%;
	position: fixed;
	z-index: -999;
	/* box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.2); */
}
  
  .hero-container {
	/* background: url('../../public/images/bordeaux.jpg') center center/cover no-repeat; */
	height: 100vh;
	width: 100%;
	display: flex;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	text-align: center;
	object-fit: contain;
	padding-top: 80px;
	box-shadow: inset 0 0 0 1000px rgba(0, 0, 0, 0.4);
}
   
  .hero-container > h1 {
	color: rgba(255, 255, 255, 0.95);
	font-size: 100px;
	margin-top: -100px;
	font-family: 'Roboto Slab', 'Times New Roman', Times, serif;
	/* animation: floating 3s ease-in-out infinite; */
  }

  /* @keyframes floating {
    0% { transform: translate(0px, 0px); }
	50% { transform: translate(0px, 10px); }
	100% { transform: translate(0px, 0px); }
  } */

  .hero-container > p {
	margin-top: 8px;
	color: #fff;
	font-size: 32px;
	font-family: 'Nunito', Arial, sans-serif;
  }
  
  .hero-btns {
	margin-top: 32px;
  }
  
  .hero-btns .btn {
	margin: 6px;
  }
  
  .fa-play-circle {
	margin-left: 4px;
  }

  @media screen and (max-width: 960px) {
	.hero-container > h1 {
	  font-size: 70px;
	  margin-top: -150px;
	}
	.hero-container > p {
		font-size: 28px;
	}
  }
  
  @media screen and (max-width: 768px) {
	.hero-container > h1 {
	  font-size: 50px;
	  margin-top: -100px;
	}
  
	.hero-container > p {
	  font-size: 25px;
	}
  
	.btn-mobile {
	  display: block;
	  text-decoration: none;
	}
  
	.btn {
	  width: 100%;
	}
  } 

  @media screen and (max-width: 450px) {
	.hero-container > h1 {
	  font-size: 30px;
	}
  
	.hero-container > p {
	  font-size: 18px;
	}
  } 