.form-wrapper {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 4rem;
	background: rgba(0, 0, 0, 0.4);
	color: #fff;
}

.form-wrapper > h1 {
	padding: 2rem;
}

.sign-up-form {
	display: flex;
	flex-direction: column;
	align-items: center;
	padding: 2rem;
}

.sign-up-form > h1 {
	padding-bottom: 1rem;
}

.inputs-wrapper {
	display: flex;
	flex-direction: row;
}

.email-input {
	border-radius: 5px;
	height: 40px;
	font-size: 1.5rem;
	background: rgb(40, 45, 50);
	color: #fff;
	padding: 10px;
	appearance: none;
	border: none;
}

.subscribe-button {
	border-radius: 5px;
	height: 40px;
	font-size: 1.4rem;
	background: rgb(45, 50, 55);
	color: #fff;
	padding: 5px;
	appearance:none;
	border:none;
	margin-left: 0.5rem;
	transition: all 0.2s ease-out;
}

.subscribe-button:hover {
	cursor: pointer;
	background: rgb(65, 70, 75);
}