.navbar {
  background: black;
  opacity: 0.95;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: fixed;
  width: 100%;
  top: 0px;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 80px;
  max-width: 1500px;
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  margin-left: 20px;
  cursor: pointer;
  text-decoration: none;
  font-size: 2rem;
  display: flex;
  align-items: center;
  transition: all 0.2s ease-out;
}

.navbar-logo:hover {
  opacity: 0.5;
}

.navbar-logo:hover .fa-sun {
  color: rgba(255, 255, 0, 0.9);
}

.fa-sun {
  margin-left: 8px;
}

.fa-typo3 {
  margin-left: 0.5rem;
  font-size: 1.8rem;
}

.nav-menu {
  display: grid;
  grid-template-columns: repeat(4, auto);
  grid-gap: 10px;
  list-style: none;
  text-align: center;
  width: 56vw; /*Change this for spacing*/
  justify-content: end;
  margin-right: 2rem;
}

.nav-item {
  height: 80px;
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  transition: all 0.2s ease-out;
  border-bottom: 1px solid black;
}

.nav-links:hover {
  border-bottom: 4px solid #fff;
}

.nav-links.dropdown {
  position: relative;
  padding: 0.5rem 0;
  background: black;
  z-index: 0;
  border-radius: 1px;
  display: none;
  transition: all 0.2s ease-out;
}

.nav-item.dropdown:hover .nav-links.dropdown{
  display: grid;
}

