.about-wrapper {
	padding: 4rem;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: flex-start;
	background: rgba(0, 0, 0, 0.4);
	color: rgba(255, 255, 255, 0.9);
}

.about-wrapper > h1 {
	text-align: center;
	color: #fff;
	padding-bottom: 2.5rem;
}

.timeline-bar {
	display: grid;
	justify-content: flex-start;
	align-items: flex-start;
	overflow-x: scroll;
	overflow-y: hidden;
	scroll-snap-type: x mandatory;
	width: 100%;
	padding-bottom: 1rem;
	margin-bottom: 2rem;
}

.body-grid {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	justify-content: center;
	gap: 1rem;
	width: 100%;
}

.about-grid-column {
	padding: 0.5rem;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	text-align: left;
}

.about-grid-column > h2 {
	text-align: center;
	padding-bottom: 2rem;
}

.about-grid-column > p {
	text-align: left;
	padding-bottom: 1rem;
}

@media screen and (max-width: 600px) {
	.body-grid {
		grid-template-columns: 100%;
	}
}